@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-regular.woff2") format("woff2"), url("../fonts/montserrat-regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-medium.woff2") format("woff2"), url("../fonts/montserrat-medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-semibold.woff2") format("woff2"), url("../fonts/montserrat-semibold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "montserrat", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  min-width: 320px;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 50px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 46px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  vertical-align: middle;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn--transparent {
  background-color: transparent;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.main-header {
  position: relative;
  width: 100%;
  z-index: 1;
}

.main__block {
  position: relative;
  background-color: #4772ac;
  color: #000000;
  background-image: url("../img/background/main-background-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main__block {
    box-shadow: none;
  }
}
@media (min-width: 1024px) and (min-resolution: 144dpi), (min-width: 1024px) and (min-resolution: 1.5dppx) {
  .main__block {
    background-image: url("../img/background/main-background-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .main__block {
    background-image: url("../img/background/main-background-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .main__block {
    background-image: url("../img/background/main-background-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .main__block {
    background-image: url("../img/background/main-background-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .main__block {
    background-image: url("../img/background/main-background-mobile@2x.jpg");
  }
}

.main__block__wrapper {
  display: inline-block;
  vertical-align: middle;
  padding: 270px 0 279px 8%;
}
@media (max-width: 1023px) {
  .main__block__wrapper {
    padding: 270px 0 274px 0;
  }
}
@media (max-width: 767px) {
  .main__block__wrapper {
    padding: 176px 0 178px;
  }
}

.main__block__wrapper h2 {
  margin: 0 0 11px 0;
  padding: 0;
  font-family: "montserrat", "Arial", sans-serif;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main__block__wrapper h2 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .main__block__wrapper h2 {
    font-size: 36px;
    line-height: 125%;
  }
}

.main__block__wrapper p {
  margin: 0;
  padding: 0;
  font-family: "montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  color: #f9fbfd;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main__block__wrapper p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .main__block__wrapper p {
    font-size: 16px;
    line-height: 140%;
    max-width: 232px;
  }
}

.main-nav {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  position: absolute;
  right: 0;
  left: 0;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .main-nav {
    padding-top: 28px;
  }
}
@media (max-width: 767px) {
  .main-nav {
    padding-top: 20px;
  }
}

.main-nav__logo:hover .main-nav__image {
  opacity: 0.5;
}

.main-nav__logo:active .main-nav__image {
  fill: #000000;
  background-color: #4772ac;
}

.main-nav__image {
  display: block;
  width: 100%;
  height: 100%;
  fill: #f9fbfd;
}

.main-nav__list {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 33px;
  margin: 0 0 0 auto;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .main-nav__list {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-nav__list {
    display: none;
  }
}

.main-nav__link {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
.main-nav__link:hover {
  opacity: 0.5;
}
.main-nav__link:active {
  color: #000000;
  background-color: #4772ac;
}
.main-nav__link:focus {
  opacity: 0.5;
  outline: none;
}

.main-nav__btn {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__btn {
    display: block;
    position: absolute;
    right: 3px;
    top: 13px;
    padding: 10px 12px;
  }
  .main-nav__btn:hover {
    opacity: 0.5;
  }
  .main-nav__btn:active {
    background-color: #4772ac;
  }
  .main-nav__btn:focus-visible {
    outline: none;
  }
  .main-nav__btn::before {
    position: absolute;
    content: "";
    background-image: url("../img/svg/close.svg");
    background-color: #f9fbfd;
    background-repeat: no-repeat;
    background-position: center;
    width: 15px;
    height: 15px;
    display: none;
    right: 19px;
    top: 11px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened {
    min-height: 568px;
    align-items: flex-start;
    position: absolute;
    right: 0;
    top: 0;
    left: inherit;
    width: 89vw;
    background-color: #f9fbfd;
  }
  .main-nav--opened::before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: -25vw;
    background: rgba(0, 43, 102, 0.5);
    z-index: -1;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 26px;
    position: absolute;
    left: 15px;
    top: 84px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__logo {
    margin-left: -1px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__btn {
    padding: 19px 26px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__btn > svg {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__btn::before {
    display: block;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__btn:focus-visible {
    background-color: #4772ac;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
    padding: 10px 0 8px 10px;
  }
}

@media (max-width: 767px) {
  .main-nav--opened .main-nav__image {
    fill: #011c40;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs {
    position: inherit;
    align-items: flex-start;
    padding-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__btn {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__btn::before {
    display: none;
  }
}

.main-nav--nojs .main-nav__image {
  fill: #011c40;
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 26px;
    margin: 0 auto;
  }
}

@media (max-width: 767px) {
  .main-nav--nojs .main-nav__link {
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
    padding: 10px 0 8px 10px;
  }
}

