.booking__checkbox-input[type=checkbox]:focus + .booking__control-mark {
  background-color: #f9fbfd;
  outline: 2px solid #a0bce1;
  outline-offset: -2px;
}

.contacts__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 120px 0 113px 0;
}
@media (max-width: 1023px) {
  .contacts__wrapper {
    padding: 100px 0 97px 0;
  }
}
@media (max-width: 767px) {
  .contacts__wrapper {
    flex-direction: column;
    padding: 44px 0 67px 0;
  }
}

.contacts__map {
  position: relative;
  width: 100%;
  max-width: 560px;
  height: 0;
  padding-bottom: 27%;
  margin: 0 30px 0 0;
}
@media (max-width: 1023px) {
  .contacts__map {
    padding-bottom: 50%;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    padding-bottom: 109%;
    margin: 0;
    order: 1;
  }
}

.contacts__map iframe {
  border: none;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.contacts__map--not-js {
  padding-bottom: 0;
  height: 100%;
  position: relative;
}
.contacts__map--not-js::before {
  position: absolute;
  content: "";
  background-image: url("../img/sprite/address.svg");
  background-repeat: no-repeat;
  width: 18px;
  height: 22px;
  left: 45%;
  bottom: 23%;
}
@media (max-width: 1023px) {
  .contacts__map--not-js {
    padding-bottom: 0;
    height: 335px;
  }
  .contacts__map--not-js::before {
    left: 40%;
  }
}
@media (max-width: 767px) {
  .contacts__map--not-js {
    padding-bottom: 0;
    height: 314px;
  }
}

.contacts__map--not-js .contacts__image {
  height: 100%;
}

.contacts__data {
  display: inline-block;
  vertical-align: middle;
  max-width: 500px;
  width: 100%;
  margin: 14px 0 0 0;
}
@media (max-width: 767px) {
  .contacts__data {
    margin: 14px 0 13px 0;
  }
}

.contacts__data p {
  margin: 0 0 33px 0;
  font-family: "montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 150%;
  color: #011c40;
}
@media (max-width: 1023px) {
  .contacts__data p {
    margin: 0 0 27px 0;
  }
}

.contacts__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.contacts__item {
  padding: 0 0 0 28px;
  margin: 0 0 22px 0;
}

.contacts__link {
  position: relative;
  font-family: "montserrat", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #011c40;
}
.contacts__link:hover {
  background-color: #a0bce1;
}
.contacts__link:active {
  background-color: #a0bce1;
}
.contacts__link:focus {
  outline: none;
  background-color: #a0bce1;
}

.contacts__link:hover svg,
.contacts__link:active svg {
  fill: #a0bce1;
}

.contacts__link:focus svg {
  fill: #a0bce1;
}

.contacts__link br {
  display: none;
}
@media (max-width: 1023px) {
  .contacts__link br {
    display: block;
  }
}

.contacts__link svg {
  position: absolute;
  fill: #011c40;
  left: -28px;
}
@media (max-width: 1023px) {
  .contacts__link svg {
    top: 3px;
  }
}

@media (max-width: 767px) {
  .contacts__link-phone svg {
    top: 0;
  }
}

